

.main_header{
  color:white;
  font-size: 70px;
  font-family:"Segoe UI";
  
}

.wind_header{
  color:white;
  font-size: 20px;
  font-family:"Segoe UI"; 
}

.weather_bar{
  width:20px;
  height:80px;
  background-color: white;
  border-radius: 50px;
}



.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
